html, body {
	margin: 0;
	padding: 0;
}

#comp {
	width: 60%;
	margin-left: 20%;
}

@media all and (max-width: 600px) {
	#comp {
		width: 95%;
		margin-left: 2.5%;
	}
}

* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
}

#root {
    position: fixed;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

#root .card_block {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.card {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}

.card_text {
    padding: 20px;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;
}

img {
    height: auto;
    width: 289px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

label {
	display: block;
    border: 2px solid #20c997;
    color: #20c997;
    border-radius: 5px;
    cursor: pointer;
    padding: 15px;
    position: absolute;
    bottom: 20px;
    left: 20px;
}

label:hover {
	background-color: #20c997;
	color: #fff;
}

input {
	display: none;
}

.btn {
    border: 2px solid #20c997;
    color: #20c997;
    border-radius: 5px;
    cursor: pointer;
    padding: 15px;
    position: absolute;
    bottom: 20px;
    right: 20px;
}

.btn-center {
    bottom: 20px;
    right: 160px;
}

.btn:hover {
	background-color: #20c997;
	color: #fff;
}

.weather_block {
	display: block;
    border: 2px solid #20c997;
    color: #20c997;
    border-radius: 5px;
    cursor: pointer;
    padding: 15px;
    position: absolute;
    top: 20px;
    left: 150px;
}

img#api {
    height: 1.5rem;
    width: auto;
}

